.registrations-container {
    padding: 20px;
  }
  
  .registered-skills-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .registered-skill-card {
    border: 1px solid #ddd;
    padding: 20px;
    border-radius: 8px;
    width: 300px;
    background-color: #f9f9f9;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
  }
  