.form-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  max-width: 600px;
  margin: auto;
}

.title {
  font-weight: bold;
  margin-bottom: 20px;
}

.input-field {
  margin: 10px 0;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  width: 100%;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  margin: 10px 0;
}

.skill-tag {
  background: #f0f0f0;
  border-radius: 15px;
  padding: 5px 10px;
  margin: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.skill-tag.selected {
  background: #6e8efb;
  color: white;
}

.selected-skills {
  margin: 10px 0;
}

.selected-skill {
  display: inline-flex;
  align-items: center;
  background: #e0e0e0;
  border-radius: 15px;
  padding: 5px 10px;
  margin: 5px;
}

.remove-skill {
  margin-left: 5px;
  cursor: pointer;
  color: red;
}

.submit-button {
  background-color: #6e8efb;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-button:hover {
  background-color: #5a7bd8;
}
