.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.login-form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 40%;
  background: rgba(156, 219, 242, 0.46);
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(42, 199, 234, 0.374);
  font-family: Ubuntu;
}

.login-field {
  margin-bottom: 20px;
  width: 80%;
  padding: 10px;
  border-radius: 5px;
  border: none;
}

.link {
  color: #6e8efb;
  cursor: pointer;
  margin-top: 10px;
  text-align: center;
}
