.dashboard-container1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.body1 {
  width: 90%;
  max-width: 1200px;
  margin: 20px auto;
}

.skills-list1 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.skill-card1 {
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  margin: 10px;
  width: 30%;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
}

.skill-card1:hover {
  transform: scale(1.05);
}

.skill-card1 h3 {
  font-size: 20px;
  margin-bottom: 10px;
  color: #333;
}

.skill-card1 p {
  margin: 5px 0;
  color: #666;
}

.join-button1 {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.join-button1:hover {
  background-color: #0056b3;
}

.error-message {
  color: red;
  font-size: 16px;
}

.header-section1 {
  position: relative;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  color: white;
}

.background-video1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.overlay1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.intro-content1 {
  text-align: center;
  max-width: 800px;
  padding: 20px;
}

.intro-content1 h1 {
  font-size: 48px;
  font-weight: 700;
  margin-bottom: 20px;
}

.intro-content1 p {
  font-size: 30px;
  margin-bottom: 40px;
  color: yellow;
  font-family: "Ubuntu", sans-serif;
}

.custom-btn1 {
  display: inline-block;
  padding: 12px 30px;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 30px;
  transition: background-color 0.3s ease;
}

.custom-btn1:hover {
  background-color: #0056b3;
}

.skill-filter1 {
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ddd;
}

@media (max-width: 768px) {
  .intro-content1 h1 {
    font-size: 36px;
  }

  .intro-content1 p {
    font-size: 16px;
  }

  .skill-card1 {
    width: 100%;
  }
}
